.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app-footer {
  text-align: center;
  background-color: #282c34;
  color: #fff;
  font-size: 0.9rem;
  padding: 15px 10px;
  border-top: 1px solid #ddd;
  margin-top: 20px;
}

.page-content {
  min-height: calc(100vh - 120px); /* Ensures footer sticks to bottom */
}

